"use client"

import { useState, useEffect, useRef } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Badge } from "@/components/ui/badge"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { ScrollArea } from "@/components/ui/scroll-area"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import { Textarea } from "@/components/ui/textarea"
import {
  MessageSquare,
  Send,
  Volume2,
  VolumeX,
  AlertTriangle,
  Users,
  Globe,
  Filter,
  Search,
  Megaphone,
  Eye,
  EyeOff,
  Clock,
  Hash
} from "lucide-react"
import { mockChatMessages, mockPlayers, type ChatMessage } from "@/lib/minecraft-data"

interface ExtendedChatMessage extends ChatMessage {
  type: 'chat' | 'join' | 'leave' | 'death' | 'achievement' | 'command'
  isStaff?: boolean
}

export default function ChatPage() {
  const [messages, setMessages] = useState<ExtendedChatMessage[]>([
    ...mockChatMessages.map(msg => ({ ...msg, type: 'chat' as const })),
    {
      id: '4',
      player: 'Sistema',
      message: 'Alex_Miner entrou no servidor',
      timestamp: new Date(Date.now() - 90000),
      world: 'lobby',
      type: 'join'
    },
    {
      id: '5',
      player: 'Sistema',
      message: 'Steve_Builder morreu para um Creeper',
      timestamp: new Date(Date.now() - 45000),
      world: 'world',
      type: 'death'
    }
  ])
  
  const [newMessage, setNewMessage] = useState("")
  const [selectedWorld, setSelectedWorld] = useState("all")
  const [chatMuted, setChatMuted] = useState(false)
  const [broadcastDialog, setBroadcastDialog] = useState(false)
  const [broadcastMessage, setBroadcastMessage] = useState("")
  const [searchTerm, setSearchTerm] = useState("")
  const [showStaffOnly, setShowStaffOnly] = useState(false)
  const messagesEndRef = useRef<HTMLDivElement>(null)

  const worlds = ["all", "world", "world_nether", "world_the_end", "lobby"]
  
  const filteredMessages = messages.filter(msg => {
    const matchesWorld = selectedWorld === "all" || msg.world === selectedWorld
    const matchesSearch = searchTerm === "" || 
      msg.message.toLowerCase().includes(searchTerm.toLowerCase()) ||
      msg.player.toLowerCase().includes(searchTerm.toLowerCase())
    const matchesStaffFilter = !showStaffOnly || msg.isStaff
    
    return matchesWorld && matchesSearch && matchesStaffFilter
  })

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }

  useEffect(() => {
    scrollToBottom()
  }, [filteredMessages])

  // Simular mensagens em tempo real
  useEffect(() => {
    const interval = setInterval(() => {
      const randomMessages = [
        "Alguém quer trocar diamantes por ferro?",
        "Onde fica a loja de itens?",
        "Preciso de ajuda com redstone",
        "Quem quer fazer uma farm comigo?",
        "Achei uma dungeon!",
        "Alguém viu meu cavalo?",
        "Como faço para proteger minha casa?"
      ]
      
      const randomPlayers = ["Steve_Builder", "Alex_Miner", "Noob123", "ProGamer", "BuilderMaster"]
      const randomWorlds = ["world", "world_nether", "lobby"]
      
      if (Math.random() > 0.7) { // 30% chance de nova mensagem
        const newMsg: ExtendedChatMessage = {
          id: Date.now().toString(),
          player: randomPlayers[Math.floor(Math.random() * randomPlayers.length)],
          message: randomMessages[Math.floor(Math.random() * randomMessages.length)],
          timestamp: new Date(),
          world: randomWorlds[Math.floor(Math.random() * randomWorlds.length)],
          type: 'chat'
        }
        
        setMessages(prev => [...prev, newMsg])
      }
    }, 10000) // A cada 10 segundos

    return () => clearInterval(interval)
  }, [])

  const sendMessage = () => {
    if (!newMessage.trim()) return

    const message: ExtendedChatMessage = {
      id: Date.now().toString(),
      player: 'Admin',
      message: newMessage,
      timestamp: new Date(),
      world: selectedWorld === "all" ? "global" : selectedWorld,
      type: 'chat',
      isStaff: true
    }

    setMessages(prev => [...prev, message])
    setNewMessage("")
  }

  const sendBroadcast = () => {
    if (!broadcastMessage.trim()) return

    const message: ExtendedChatMessage = {
      id: Date.now().toString(),
      player: 'Sistema',
      message: `[BROADCAST] ${broadcastMessage}`,
      timestamp: new Date(),
      world: 'global',
      type: 'chat',
      isStaff: true
    }

    setMessages(prev => [...prev, message])
    setBroadcastMessage("")
    setBroadcastDialog(false)
  }

  const getMessageIcon = (type: string) => {
    switch (type) {
      case 'join': return '🟢'
      case 'leave': return '🔴'
      case 'death': return '💀'
      case 'achievement': return '🏆'
      case 'command': return '⚡'
      default: return '💬'
    }
  }

  const getMessageColor = (type: string, isStaff?: boolean) => {
    if (isStaff) return 'text-blue-400'
    
    switch (type) {
      case 'join': return 'text-green-400'
      case 'leave': return 'text-red-400'
      case 'death': return 'text-orange-400'
      case 'achievement': return 'text-yellow-400'
      case 'command': return 'text-purple-400'
      default: return 'text-gray-300'
    }
  }

  const formatTime = (date: Date) => {
    return date.toLocaleTimeString('pt-BR', { 
      hour: '2-digit', 
      minute: '2-digit',
      second: '2-digit'
    })
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-white">Chat do Servidor</h1>
          <p className="text-gray-400">Monitore e gerencie o chat em tempo real</p>
        </div>
        <div className="flex items-center space-x-2">
          <Badge variant={chatMuted ? "destructive" : "secondary"}>
            {chatMuted ? "Chat Mutado" : "Chat Ativo"}
          </Badge>
          <Button
            variant="ghost"
            size="sm"
            onClick={() => setChatMuted(!chatMuted)}
            className={chatMuted ? "text-red-400" : "text-green-400"}
          >
            {chatMuted ? <VolumeX className="h-4 w-4" /> : <Volume2 className="h-4 w-4" />}
          </Button>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
        {/* Chat Principal */}
        <div className="lg:col-span-3">
          <Card className="bg-gray-800 border-gray-700 h-[600px] flex flex-col">
            <CardHeader className="pb-3">
              <div className="flex items-center justify-between">
                <CardTitle className="text-white flex items-center">
                  <MessageSquare className="mr-2 h-5 w-5" />
                  Chat em Tempo Real
                </CardTitle>
                <div className="flex items-center space-x-2">
                  <Select value={selectedWorld} onValueChange={setSelectedWorld}>
                    <SelectTrigger className="w-32 bg-gray-700 border-gray-600">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent className="bg-gray-700 border-gray-600">
                      <SelectItem value="all">Todos</SelectItem>
                      <SelectItem value="world">Overworld</SelectItem>
                      <SelectItem value="world_nether">Nether</SelectItem>
                      <SelectItem value="world_the_end">End</SelectItem>
                      <SelectItem value="lobby">Lobby</SelectItem>
                    </SelectContent>
                  </Select>
                  
                  <Button
                    variant="ghost"
                    size="sm"
                    onClick={() => setShowStaffOnly(!showStaffOnly)}
                    className={showStaffOnly ? "text-blue-400" : "text-gray-400"}
                  >
                    {showStaffOnly ? <Eye className="h-4 w-4" /> : <EyeOff className="h-4 w-4" />}
                  </Button>
                </div>
              </div>
              
              {/* Search */}
              <div className="relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                <Input
                  placeholder="Buscar mensagens..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="pl-10 bg-gray-700 border-gray-600"
                />
              </div>
            </CardHeader>
            
            <CardContent className="flex-1 flex flex-col p-0">
              {/* Messages */}
              <ScrollArea className="flex-1 p-4">
                <div className="space-y-2">
                  {filteredMessages.map((message) => (
                    <div key={message.id} className="flex items-start space-x-3 p-2 rounded hover:bg-gray-700/50">
                      <div className="flex-shrink-0 mt-1">
                        <span className="text-sm">{getMessageIcon(message.type)}</span>
                      </div>
                      
                      <div className="flex-1 min-w-0">
                        <div className="flex items-center space-x-2">
                          <span className={`font-medium text-sm ${getMessageColor(message.type, message.isStaff)}`}>
                            {message.player}
                          </span>
                          <Badge variant="outline" className="text-xs">
                            {message.world}
                          </Badge>
                          <span className="text-xs text-gray-500">
                            {formatTime(message.timestamp)}
                          </span>
                        </div>
                        <p className="text-sm text-gray-300 mt-1 break-words">
                          {message.message}
                        </p>
                      </div>
                    </div>
                  ))}
                  <div ref={messagesEndRef} />
                </div>
              </ScrollArea>
              
              {/* Message Input */}
              <div className="p-4 border-t border-gray-700">
                <div className="flex space-x-2">
                  <Input
                    placeholder={chatMuted ? "Chat está mutado" : "Digite sua mensagem..."}
                    value={newMessage}
                    onChange={(e) => setNewMessage(e.target.value)}
                    onKeyPress={(e) => e.key === 'Enter' && sendMessage()}
                    disabled={chatMuted}
                    className="bg-gray-700 border-gray-600"
                  />
                  <Button 
                    onClick={sendMessage} 
                    disabled={!newMessage.trim() || chatMuted}
                    className="bg-blue-600 hover:bg-blue-700"
                  >
                    <Send className="h-4 w-4" />
                  </Button>
                  
                  <Dialog open={broadcastDialog} onOpenChange={setBroadcastDialog}>
                    <DialogTrigger asChild>
                      <Button variant="ghost" className="text-yellow-400 hover:text-yellow-300">
                        <Megaphone className="h-4 w-4" />
                      </Button>
                    </DialogTrigger>
                    <DialogContent className="bg-gray-800 border-gray-700">
                      <DialogHeader>
                        <DialogTitle className="text-white">Enviar Broadcast</DialogTitle>
                        <DialogDescription className="text-gray-400">
                          Envie uma mensagem para todos os jogadores online
                        </DialogDescription>
                      </DialogHeader>
                      
                      <Textarea
                        placeholder="Digite sua mensagem de broadcast..."
                        value={broadcastMessage}
                        onChange={(e) => setBroadcastMessage(e.target.value)}
                        className="bg-gray-700 border-gray-600"
                      />
                      
                      <DialogFooter>
                        <Button variant="ghost" onClick={() => setBroadcastDialog(false)}>
                          Cancelar
                        </Button>
                        <Button 
                          onClick={sendBroadcast}
                          disabled={!broadcastMessage.trim()}
                          className="bg-yellow-600 hover:bg-yellow-700"
                        >
                          Enviar Broadcast
                        </Button>
                      </DialogFooter>
                    </DialogContent>
                  </Dialog>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Sidebar */}
        <div className="space-y-6">
          {/* Chat Stats */}
          <Card className="bg-gray-800 border-gray-700">
            <CardHeader>
              <CardTitle className="text-white text-lg">Estatísticas</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="flex justify-between">
                <span className="text-gray-400">Mensagens hoje:</span>
                <span className="text-white font-medium">1,247</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-400">Jogadores ativos:</span>
                <span className="text-white font-medium">8</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-400">Mensagens/min:</span>
                <span className="text-white font-medium">3.2</span>
              </div>
            </CardContent>
          </Card>

          {/* Quick Actions */}
          <Card className="bg-gray-800 border-gray-700">
            <CardHeader>
              <CardTitle className="text-white text-lg">Ações Rápidas</CardTitle>
            </CardHeader>
            <CardContent className="space-y-2">
              <Button 
                className="w-full justify-start" 
                variant="ghost"
                onClick={() => setChatMuted(!chatMuted)}
              >
                {chatMuted ? <Volume2 className="mr-2 h-4 w-4" /> : <VolumeX className="mr-2 h-4 w-4" />}
                {chatMuted ? "Ativar Chat" : "Mutar Chat"}
              </Button>
              
              <Button className="w-full justify-start" variant="ghost">
                <AlertTriangle className="mr-2 h-4 w-4" />
                Limpar Chat
              </Button>
              
              <Button 
                className="w-full justify-start" 
                variant="ghost"
                onClick={() => setBroadcastDialog(true)}
              >
                <Megaphone className="mr-2 h-4 w-4" />
                Broadcast
              </Button>
            </CardContent>
          </Card>

          {/* Active Worlds */}
          <Card className="bg-gray-800 border-gray-700">
            <CardHeader>
              <CardTitle className="text-white text-lg">Mundos Ativos</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-2">
                {worlds.slice(1).map((world) => {
                  const worldMessages = messages.filter(m => m.world === world).length
                  return (
                    <div key={world} className="flex justify-between items-center">
                      <span className="text-gray-300">{world}</span>
                      <Badge variant="secondary">{worldMessages}</Badge>
                    </div>
                  )
                })}
              </div>
            </CardContent>
          </Card>

          {/* Recent Players */}
          <Card className="bg-gray-800 border-gray-700">
            <CardHeader>
              <CardTitle className="text-white text-lg">Jogadores Recentes</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-2">
                {mockPlayers.filter(p => p.isOnline).slice(0, 3).map((player) => (
                  <div key={player.id} className="flex items-center space-x-2">
                    <Avatar className="h-6 w-6">
                      <AvatarImage src={player.avatar} />
                      <AvatarFallback>{player.username[0]}</AvatarFallback>
                    </Avatar>
                    <span className="text-sm text-gray-300">{player.username}</span>
                    <div className="w-2 h-2 bg-green-500 rounded-full" />
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}
